.none{
    display: none;
}

.floatLeft{
    float: left;
    text-align: left;
    margin-bottom: 1vh;
}

.floatRight{
    float: right;
    text-align: right;
    margin-bottom: 1vh;
}

.rainbow-m-around_large {
    margin: 2vh 1vw;
    min-width: 15vw;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -khtml-user-select: none;
}

.temp-container{
    display: inline-flex;
    margin-top: 2vh;
    padding-left: 1vw;
    padding-right: 1vw;
}

.tempMin{
    text-align: left;
    margin-right: 5vw;
}

.tempMax{
    text-align: right;
}

.rainbow-flex_column{
    text-align: center;
}
