@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300&display=swap);
header {
    text-align: center;
}

.center{
    display: flex;
    justify-content: center;
}
.none{
    display: none;
}

.floatLeft{
    float: left;
    text-align: left;
    margin-bottom: 1vh;
}

.floatRight{
    float: right;
    text-align: right;
    margin-bottom: 1vh;
}

.rainbow-m-around_large {
    margin: 2vh 1vw;
    min-width: 15vw;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -khtml-user-select: none;
}

.temp-container{
    display: inline-flex;
    margin-top: 2vh;
    padding-left: 1vw;
    padding-right: 1vw;
}

.tempMin{
    text-align: left;
    margin-right: 5vw;
}

.tempMax{
    text-align: right;
}

.rainbow-flex_column{
    text-align: center;
}

.centerTitle{
    text-align: center;
}

.container{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.Loader {
    border: 16px solid #f3f3f3;
    border-top: 16px solid rgb(90, 89, 89);
    border-radius: 50%;
    width: 50px;
    height: 50px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
    margin: 10px auto;
}

@-webkit-keyframes spin {
    0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes spin {
    0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
.rainbow-m-around_medium {
    margin-top: 2vh;
    left: 50%;
    margin-left: -74px;
}
html{
	overflow: none;
}

body{
	font-family: 'Source Sans Pro', sans-serif;
	font-weight: 400;
	font-size: 1;
	color: #e0e0e0;
	background: no-repeat url(/forecast-app/static/media/clouds-blue.fe6cd0d8.jpg);
	background-size: cover;
}

.pointer{
	cursor: pointer;
}

h1{	/* App Title, City Title*/
	font-weight: 700;
	font-size: 2.5rem;
    margin: 0;
}

h2{ /* App Desc */
	font-weight: 400;
	font-size: 1.5rem;
}

h3 { /* Card temp title */
    font-size: 2rem;
    padding: 0 .5vw;
}

ul{
    list-style-type: none;
    padding: 0;
}

li{
    font-size: 1.15rem;
}

.dmTruq{ /* Search bar Label */
	font-style: italic;
	font-size: 1rem !important;
	font-weight: 300;
	color: #e0e0e0 !important;
}

.centered {
	display: flex;
	flex-direction: column;
}

.day-info-column{
	font-size: 1.25rem;
}

#input-4{
	background-color: rgba(255, 255, 255, .5) !important;
	-webkit-backdrop-filter: blur(5px) !important;
	        backdrop-filter: blur(5px) !important;
}

#loadingButton{
	background-color: rgba(255, 255, 255, .65);
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
}

#button{
	background-color: rgba(255, 255, 255, .65);
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
}

.sc-btzYZH.dBgyJd{
	background-color: rgba(255, 255, 255, .5) !important; 
	-webkit-backdrop-filter: blur(5px) !important; 
	        backdrop-filter: blur(5px) !important;
}

@media (max-height: 700px){
	h1{
		font-size: 1.75rem;
	}

	h2{
		font-size: 1rem;
	}

	h3{
		font-size: 1.5rem;
	}


	br{
		display: none;
	}

	ul{
		margin: 0;
	}

	li{
		font-size: 0.75rem;
	}

	.sc-iELTvk, .yWjWt{
		margin: 0;
		padding: 5px;
	}

	.day-info-column{
		font-size: 1rem;
	}

	.rainbow-flex{
		display: none;
	}

	.rainbow-p-top_large{
		margin: 0;
	}
}
